*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  font-family: 'Atkinson Hyperlegible', sans-serif;
  color: #2a2a2a;
}

img {
  display: block;
  /* max-width: 100%; */
  width: 100%;
  /* height: auto; */
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

.search-form {
  display: flex;
  justify-content: center;
  padding: 20px 0 20px 0;
}

.search-input {
  width: 300px;
  height: 30px;
  border: 1px solid #2196f3;
  border-radius: 4px;

  font-weight: 400;
  font-size: 15px;

  background-color: transparent;
}
.search-input:focus {
  border-color: #2196f3;
}
.search-inpu::placeholder {
  color: #2196f3;
}

.button {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

/* .button.is-hidden {
  display: none;
}

.button > .spinner.is-hidden {
  display: none;
} */
.gallery-list {
  display: flex;
  flex-wrap: wrap;
  margin-right: -30px;
  margin-bottom: -30px;
}
.gallery-item {
  display: flex;
  /* width: 400px; */
  flex-basis: calc(100% / 3 - 30px);
  margin-right: 30px;
  margin-bottom: 30px;
}

.photo-card {
  display: flex;
  flex-direction: column;
  height: 300px;
}

.gallery-image-box {
  overflow: hidden;
}

.gallery-image {
  height: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  overflow: hidden;
}
.gallery-image:hover {
  transform: scale(1.4);
  border-radius: 4px;
}

.stats {
  display: flex;
  justify-content: center;
  align-items: center;
}

.stats-item {
  padding: 15px 8px 15px 8px;
}

.material-icons {
  display: flex;
  justify-content: center;
  align-items: center;

  /* width: 10px;
  height: 10px; */

  color: #2196f3;
}
